.App {
  position: relative;
  float: left;

  display: block;
  width: 100%;
  min-height: 100%;

  background-attachment: fixed;
  background-position: top center;
  background-repeat: no-repeat;
}

@media screen and (min-width: breakpoint('hd')) {
  .App {
    background-size: cover;
  }
}


